.custom-dark-bg {
    background-color: #333; /* Fondo oscuro */
    color: #fff; /* Color de texto claro */
}

.custom-bg-slate-100 {
    background-color: #f3f4f6; /* Fondo de color slate-100 */
}

.custom-container {
    max-width: 100%; /* Ancho máximo del contenedor */
    margin: 0 auto; /* Centrar el contenedor horizontalmente */
    padding: 0 20px; /* Relleno interno del contenedor */
    box-sizing: border-box; /* Incluir padding en el ancho total */
}

.custom-container img {
    max-width: 100%; /* Ajustar el ancho máximo de la imagen al contenedor */
    height: auto; /* Mantener la proporción de la imagen */
    display: block; /* Hacer que la imagen sea un bloque */
    margin: 0 auto; /* Centrar la imagen horizontalmente */
    border-radius: 14px;
}
 


.custom-grid {
    display: grid; /* Mostrar como cuadrícula */
    grid-gap: 20px; /* Espacio entre elementos de la cuadrícula */
}

.custom-grid-cols-1 {
    grid-template-columns: 1fr; /* Una columna en la cuadrícula */
}

.custom-sm-grid-cols-2 {
    grid-template-columns: 1fr; /* Una columna en dispositivos pequeños */
}

.custom-space-y-5 {
    margin-bottom: 20px; /* Espacio entre elementos */
}

.custom-sm-p-16 {
    padding: 16px; /* Relleno en dispositivos pequeños */
}

.custom-pb-6 {
    padding-bottom: 6px; /* Relleno en la parte inferior */
}

.custom-text-3xl {
    font-size: 1.5rem; /* Tamaño de texto grande */
}

.custom-sm-text-4xl {
    font-size: 2rem; /* Tamaño de texto más grande en dispositivos pequeños */
}

.custom-font-serif {
    font-family: "Abel", sans-serif;
    font-weight: 400;
    font-style: normal;
}

.custom-secondary {
    color: #666; /* Color de texto secundario */
}

@media (min-width: 640px) {
    .custom-grid {
        grid-template-columns: repeat(2, 1fr); /* Dos columnas en dispositivos medianos y más grandes */
     
    }
}

@media (min-width: 768px) {
    .custom-sm-p-16 {
        padding: 20px; /* Aumentar el relleno en dispositivos medianos */
    }
}

@media (min-width: 1024px) {
    .custom-container {
        padding: 0 40px; /* Aumentar el relleno interno del contenedor en dispositivos grandes */
    }
}
