/* Asesores.css */

.container-asesores {
  max-width: 1200px;
  margin: 0 auto;
}

.titulo-asesores {
  margin-bottom: 1rem;
}

.mensaje-ayuda {
  margin-bottom: 2rem;
}

.grid {
  display: grid;
}

.card-asesor {
  border-radius: 1rem;
}

.card-asesor img {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

.card-asesor .p-4 {
  padding: 1rem;
}
