.v {
  margin:1rem;
  font-family: "Abel", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.w {
  font-size: 1.75rem;
  font-weight: bold;
}

.x {
  color: #000;
  font-size: 1.125rem;
}

.y {
  background-color: #ffffff;
  color: #000000;
}

.z {
  min-height: 620px;
}

.a {
  display: grid;
  grid-template-columns: 1fr;
}

.b {
  @media (min-width: 640px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

.c {
  place-items: center;
}

.d {
  order: 1;
}

.e {
  @media (min-width: 640px) {
    order: 2;
  }
}

.f {
  width: 100%;
  height: 400px;
}

.g {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 14px;
}

.h {
  order: 2;
}

.i {
  @media (min-width: 640px) {
    order: 1;
  }
}

.j {
  @media (min-width: 640px) {
    padding-right: 2rem;
  }
}
