.tarifas-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.card {
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-bottom: 20px;
  width: 100%;
  max-width: 600px; /* Máximo ancho para evitar que se extienda demasiado en pantallas grandes */
  transition: all 0.1s ease; /* Transición suave para los cambios */
}

.tarifa-card {
  margin-bottom: 10px;
}

h2 {
  margin-bottom: 20px; /* Más espacio entre el título y el contenido */
}

p {
  margin: 0;
}

.card:hover {
  transform: translateY(-1px); /* Efecto de levantamiento al pasar el ratón */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Sombra más pronunciada */
  border: 1px solid #ffc727; /* Borde en relieve al pasar el ratón */
}

@media screen and (max-width: 600px) {
  .tarifas-container {
    padding: 10px; /* Menos espacio en dispositivos pequeños */
  }

  .card {
    padding: 15px;
  }

  h2 {
    margin-bottom: 15px;
  }
}
