/* VariedadMotos.css */

.variedad-motos-container {
    max-width: 800px;
    margin: auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    margin-top: 5rem;
  }
  
  .variedad-motos-container h2 {
    color: #333;
    font-size: 24px;
    margin-bottom: 15px;
  }
  
  .variedad-motos-container h3 {
    color: #555;
    font-size: 20px;
    margin-top: 30px;
  }
  
  .variedad-motos-container p {
    line-height: 1.5;
    margin-bottom: 15px;
  }
  
  /* Clases adicionales */
  
  .subtitulo {
    font-size: 18px;
    font-weight: bold;
    margin-top: 20px;
  }
  
  .contenido-adicional {
    background-color: #f9f9f9;
    padding: 15px;
    border-radius: 5px;
    margin-top: 20px;
  }
  