.contact-container {
    color: #353535;
    padding-top: 3.5rem;
    padding-bottom: 3.5rem;

  }
  
  .contact-content {
    background-color: #ffffff ;
    padding: 2rem;
    border-radius: 1rem;
    border: 4px solid #ffc727;
  }
  
  .contact-title {
    font-size: 2.5rem;
    font-weight: bold;
  }
  
  .contact-description {
    color: #353535;
    font-size: 1.2rem;
  }
  
  .contact-button-container {
    display: grid;
    place-items: center;
  }
  
  .contact-button {
    font-weight: bold;
    padding: 0.5rem 1.5rem;
    background-color: #ffffff;
    color: #353535;
    text-decoration: none;
    transition: background-color 0.2s ease;
    border-radius: 0.5rem;
  }
  
  .contact-button:hover {
    background-color: rgba(75, 94, 64, 0.8);
  }
  