:root {
  --primary-color: #eec43b;
}
.carousel-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 18px;
}
.carousel {
  position: relative;
  width: 100%;
  max-width: 700px;
}

.carousel-item {
  width: 100%;
  height: auto;
}

.carousel-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  padding: 10px 0;
  width: 100%;
}
.button-outline {
  background-color: transparent;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.button-outline:hover {
  background-color: var(--primary-color);
  color: #fff;
}
.button-prev {
  order: 1; 
}

.button-next {
  order: 2; 
}

.button-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 10px;
}
.btn {
  padding: 5px 10px;
  background-color: var(--primary-color); 
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #f7eb48; 
}

.btn-next {
  background-color: var(--primary-color); 
}

.btn-next:hover {
  background-color: #f7eb48; 
}

.button-container {
  display: flex;
  justify-content: center; 
  align-items: center; 
  height: 8vh; 
  margin-bottom: 20px; 
}

.button-text {
  border: 2px  #ffc727;
  padding: 10px 20px;
  background-color: #ffffff;
  width: 20rem;
  height: 3rem;
  color:  #1a1919;
  font-size: 13px;
  font-weight: bold;
  text-decoration: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  display: flex;
  flex:inherit;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.button-text h2 {
  color:  #1A1919;
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
}

.button-text .short {
  color: #111111;
  font-size: 24px;
}

.button-text:hover {
  background-color: #f0f0f0 ;
}





.banner {
  background-image: url('../../assets/banner06.svg');
  background-size: cover;
  background-position: center;
  height: 144px;
  display: flex;
  margin-top: 40px;
  align-items: center;
  justify-content: center;
  color: #ffc727;
}

@media only screen and (max-width: 768px) {
  .banner {
    height: 100px;
  }
}

@media only screen and (max-width: 480px) {
  .banner {
    background-image: url('../../assets/banner02.svg');
    height: 90px;
  }
}

.banner-content {
  text-align: center;
}

.banner-title {
  font-size: 2.5vw; /* Ajustado para ser responsive */
  margin-bottom: 0.2em;
}

.banner-subtitle {
  font-size: 1.2vw; /* Ajustado para ser responsive */
  margin-top: 0;
}






.tarifas-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.tarifas-title {
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1.5rem;
}

.tarifas-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

.tarifas-button-container {
  display: flex;
  justify-content: center;
}

.tarifas-button {
  background-color: #25D366;
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}

.tarifas-text-container {
  text-align: justify;
}

.tarifas-text {
  font-size: 1rem;
  color: #181818;
  margin-bottom: 1rem;
}

.r6j8-qlsj-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}
.r6j8-qlsj-title {
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
}
.r6j8-qlsj-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
@media (min-width: 768px) {
  .r6j8-qlsj-grid {
    grid-template-columns: 1fr 2fr;
  }
}
.r6j8-qlsj-button {
  display: block;
  width: 100%;
  padding: 10px;
  text-align: center;
  background-color: #25D366;
  color: #fdfdfd;
  text-decoration: none;
  border-radius: 5px;
}
.r6j8-qlsj-button:hover {
  background-color: #2ba746;
}
.r6j8-qlsj-text {
  font-size: 16px;
  line-height: 1.5;
}








table {
  border-collapse: collapse; 
  width: 100%; 
  margin: 0 auto; 
}

th, td {
  border: 0px 1px 1px 1px solid #ccc; 
  padding: 0.5rem; 
  text-align: center; 
}

@media screen and (max-width: 768px) {
  .carousel {
    max-width: 100%;
  }
}
